<template>
  <div id="app">
    <!-- <img src="./assets/title.jpg" id="mainBg"> -->
    <img src="/src/assets/title.jpg" id="mainBg">
    <div class="container">
      <div class="row">
  
        <div class="title" v-if="!showSuccessMsg">
          <h1 v-if="!isPass">{{msg}}</h1>
          <h1 v-else>C1課程報到</h1>
        </div>
  
        <div class="formBox" v-if="isPass && !showSuccessMsg">
          <form class="form-horizontal">
  
            <div class="form-group" style="margin-bottom:0;">
              <div class="col-sm-offset-2 col-sm-10">
                <label class="text-danger">* 必填</label>
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-2 col-sm-10">
                <div class="checkbox">
                  <label>
                    <input type="checkbox" v-model="isNotInGroup"> 我目前尚未加入小組
                  </label>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label for="groupType" class="col-sm-2 control-label">
                <span class="text-danger">* </span>組別
              </label>
              <div class="col-sm-10">
                <select name="groupType" id="groupType" class="form-control" :disabled="isNotInGroup" v-model="groupTypeName">
                  <option v-for="groupType in groupClass" :key="groupType.group">{{ groupType.group }}</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label for="group" class="col-sm-2 control-label">
                <span class="text-danger">* </span>小組
              </label>
              <div class="col-sm-10">
                <select name="group" id="group" class="form-control" :disabled="isNotInGroup" v-model="groupName">
                  <option v-for="group in groups" :value="group" :key="group.split('.')[0]">{{ group }}</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label for="name" class="col-sm-2 control-label">
                <span class="text-danger">* </span>姓名
              </label>
              <div class="col-sm-10">
                <input type="text" id="name" name="name" class="form-control" placeholder="請填寫姓名" v-validate="'required'" v-model="name">
                <span class="text-danger" v-show="errors.has('name')">{{ errors.first('name') }}</span>
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-2 col-sm-10">
                <button type="submit" class="btn btn-default" @click.prevent="submit">
                  <span v-if="!isSending">報到</span>
                  <span v-else>
                    <i class='fa fa-circle-o-notch fa-spin'></i> Sending...
                  </span>
                </button>
              </div>
            </div>
  
          </form>
        </div>

        <div class="title" v-if="showSuccessMsg">
          <h4>{{ showGroupName }}{{ name }}</h4>
          <h1>{{ new Date().toLocaleString() }}</h1>
          <h1>{{ categoryId }}課程報到成功</h1>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'app',
  data() {
    return {
      isPass: false,
      msg: '讀取中...',
      categoryId: '',
      sId: '',
      groupClass: [],
      isNotInGroup: false,
      groupTypeName: '',
      groupName: '',
      name: '',
      isSending: false,
      showSuccessMsg: false,
    }
  },
  computed: {
    groups() {
      let v = this.groupTypeName, g = this.groupClass;
      for (var i = 0, l = g.length; i < l; i++) {
        if (g[i].group == v) {
          return g[i].list;
        }
      }
      return [];
    },
    showGroupName() {
      if (this.groupName){
        return this.groupName + '-';
      }
      return '';
    }
  },
  watch: {
    isNotInGroup(value) {
      if (value) {
        this.groupTypeName = '';
        this.groupName = '';
      }
    }
  },
  methods: {
    submit() {
      if (!this.isSending) {
        let data = {}, self = this;
        //判斷小組有沒有填
        if (!self.isNotInGroup) {
          if (!self.groupTypeName || !self.groupName) {
            swal("Oops...", '請選擇組別及小組', "error");
            return;
          }
        }

        this.$validator.validateAll().then(() => {
          self.isSending = true;

          axios.post('/api/UpdSubSign.aspx', {
          // axios.post('http://localhost:3000/sendData',{
            "S_ID": self.sId,
            "Category": self.categoryId,
            "gcroup": self.groupTypeName,
            "group": self.groupName,
            "name": self.name,
          }, {
              headers: { 'content-type': 'application/json' }
            })
            .then(function (response) {
              self.isSending = false;
              self.showSuccessMsg = true;
            }).catch(function (error) {
              self.isSending = false;
              swal("Oops...", error, "error");
            });
        }).catch((error) => {
          swal("Oops...", "請確實填寫需要的資訊", "error");
        });
      }
    },
    callGetMember(Phone) {
      const api = 'api/GetMember.aspx';
      const testGetMemberApi = 'http://localhost:3000/testGetMemberApi';

      let vm = this;

      axios.post(api, { Phone }).then(function({ data }) {
      // axios.get(testGetMemberApi).then(function({ data }) {
        if (!data.MID){
          swal('您沒有資料！', '請填寫表單', 'error');
          return;
        }

        vm.name = data.Ename;
        vm.groupTypeName = data.gclass;
        vm.groupName = data.group2;

        if (!data.gclass){
          vm.isNotInGroup = true;
        }
        swal('發送手機號碼成功！', '', 'success');
      }).catch(function(error) {
        swal('發送手機號碼錯誤！', error, 'error');
      });
    },
    getParameterByName(name, url) {
      if (!url) url = window.location.href;
      name = name.replace(/[\[\]]/g, "\\$&");
      var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
          results = regex.exec(url);
      if (!results) return null;
      if (!results[2]) return '';
      return decodeURIComponent(results[2].replace(/\+/g, " "));
    },
  },
  mounted() {
    let vm = this;
    const api = '/api/GetSubSign.aspx';
    const apiPass = 'http://localhost:3000/testPass'
    const Category_ID = this.getParameterByName('id');

    axios.post(api, { Category_ID }).then(function ({ data }) {
    // axios.get(apiPass).then(function ({ data }) {

      vm.isPass = data.IsPass;
      vm.msg = data.Msg;
      vm.categoryId = data.Category_ID;
      vm.sId = data.S_ID;

      if (data.IsPass) {
        axios.get('/api/GetGroupItem.aspx').then(function ({ data }) {
        // axios.get('http://localhost:3000/GetGroupItem').then(function ({ data }) {
          vm.groupClass = data.DataInfo;

          swal({
            title: "",
            text: `
              <span class="text-danger">若輸入手機號碼可以可以幫助您加快報到速度(需輸入正確)</span>
            `,
            html: true,
            type: "input",
            showCancelButton: true,
            cancelButtonText: "取消",
            confirmButtonText: "送出",
            closeOnConfirm: false,
            inputPlaceholder: "請輸入手機號碼",
            showLoaderOnConfirm: true,
          },
          function(inputValue){
            if (inputValue === false) return false;
            
            if (inputValue === "") {
              swal.showInputError("請輸入手機號碼！");
              return false
            }

            // let cellphone = /^[09]{2}[0-9]{8}$/;
            // if (cellphone.test(inputValue.toString()) ) {
              vm.callGetMember(inputValue);
            // }else {
              // swal.showInputError("請輸入正確的手機號碼格式！");
            // }
            return false;
          });
          
        }).catch(function (error) {
          swal('系統發生錯誤', error, 'error');
        });
      }

    }).catch(function (error) {
      vm.msg = '發生錯誤!';      
      swal('系統發生錯誤', error, 'error');
    });
  }
}
</script>

<style>
#mainBg { width: 100%; }

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
}

.formInfo {
  display: inline-block;
  max-width: 600px;
  text-align: left;
  font-size: 12px;
}

ul {
  padding-left: 1.5em;
}

.form-horizontal {
  text-align: left;
  margin-bottom: 40px;
}

.formBox {
  max-width: 600px;
  margin: 0 auto;
}

.text-danger {
  font-size: 12px;
}

.title {
  color: #c6ac4b;
}
</style>



