import Vue from 'vue'
import App from './App.vue'
import sweetAlert from 'sweetalert'
import VeeValidate from 'vee-validate';

const config = {
  locale: 'tw',
  dictionary: {
    tw: {
      messages: {
        email: () => '請填寫正確 Email 格式',
        required: () => '此欄位必填',
        isGmail: () => '請填寫正確 Gmail 格式'
      }
    }
  },
};
Vue.use(VeeValidate, config);

new Vue({
  el: '#app',
  render: h => h(App)
})


